</template>

<script>
import LoadingWrap from './LoadingWrap.vue'

    export default {
        data() {
            return {
                showLogin: true,
                showRegistration: false,

                showload: false
            }
        },
        methods: {
            login() {
                this.showLogin = true
                this.showRegistration = false
                document.getElementById('form')
                .style.marginTop = '0px';
            },
            registration() {
                this.showLogin = false
                this.showRegistration = true
                document.getElementById('form')
                .style.marginTop = '7px'
            },
            sendLoginForm() {
                this.showload = true
                setTimeout(() => {
                    this.showload = false
                }, 10000);
            },
            sendRegistrationForm() {
                this.showload = true
                setTimeout(() => {
                    this.showload = false
                }, 2000);
            }
        },
        components: {
            LoadingWrap
        }
    }
</script>



